import React, {Component} from 'react';
import '../App.css';

export default class users extends Component {
    state = {
        output:'Users Details',
        list:[
            {
                name:'Richard',
                surname:'Ntjana',
                age:24,
                location:'Bochum'
            },
            {
                name:'Theresho',
                surname:'Maela',
                age:29,
                location:'Swartz'
            },
            {
                name:'Moshe',
                surname:'Leboho',
                age:36,
                location:'Polokwane'
            },
            {
                name:'Jack',
                surname:'Masha',
                age:43,
                location:'Turfloop'
            }
        ]

    }
   render() {
    return (
        <div className="user">
        <ul>
    <h1>{ this.state.output }</h1>
        {this.state.list.map(element =><li>{element.name + ' ' + element.surname + ', ' + element.age }</li>)}
  </ul>
  </div>
  );
   }
}
   
